<script>
import SwiperComponent from "@/components/front/SwiperComponent.vue";

export default {
  data() {
    return {
      galleryID: "certificates-gallery",
      strength: [],
      certificates: [],
      useToolIcons: [],
      learnToolIcons: [],
    };
  },
  props: ["detectWidth"],
  components: {
    SwiperComponent,
  },
  methods: {
    getStrength() {
      fetch(`${process.env.VUE_APP_API}/strength`)
        .then((res) => {
          return res.json();
        })
        .then((res) => {
          this.strength = res;
        })
        .catch((err) => {
          console.dir(err);
        });
    },
    getCertificates() {
      let loader = this.$loading.show();

      fetch(`${process.env.VUE_APP_API}/certificates`)
        .then((res) => {
          return res.json();
        })
        .then((res) => {
          loader.hide();
          this.certificates = res;
        })
        .catch((err) => {
          loader.hide();
          console.dir(err);
        });
    },
    getToolIcons() {
      fetch(`${process.env.VUE_APP_API}/toolIcons`)
        .then((res) => {
          return res.json();
        })
        .then((res) => {
          this.useToolIcons = res.slice(0, 11);
          this.learnToolIcons = res.slice(12);
        })
        .catch((err) => {
          console.dir(err);
        });
    },
  },
  mounted() {
    this.getCertificates();
    this.getStrength();
    this.getToolIcons();
  },
};
</script>

